/*------------------------------------*\
    #MODALS
\*------------------------------------*/
.modal-dialog {
	margin-top: 60px;
}

.modal-content {
	border-radius: 0;
	-moz-box-shadow:none;
	-webkit-box-shadow:none;
	box-shadow: none;
	text-align: left;
}

.modal-content .modal-title {
	text-transform: capitalize;
	font-family: 'Lora', sans-serif;
}
